body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{display:grid;grid-gap:1rem;gap:1rem;grid-template-columns:repeat(3, 1fr);grid-template-areas:"header header header" "contents contents sidebar" "footer footer footer";width:1024px;border:3px solid black;max-width:100%;margin:10px auto}.app .header{display:flex;justify-content:space-between;align-items:center;grid-area:header}.app .sidebar{grid-area:sidebar}.app .contents{grid-area:contents}.app .footer{grid-area:footer;justify-self:center}



.suggestion{display:grid;grid-template-columns:32px 2fr 1fr;align-items:center;margin-bottom:0.5em}.suggestion .avatar{justify-self:center;margin-right:0.5em}.suggestion .action{justify-self:right}

.PostNew{width:768px;margin:20px auto}

